/* --- note: custom CSS code --- */

/* Important class for Google-maps, it comes with google maps integration */
.pac-container {
  z-index: 1100;
}

.py-auto {
  padding-top: 0;
  padding-bottom: 0;
}

.bg-transparent {
  opacity: 1;
  background-color: transparent;
}

.border-line {
  display: flex;
  height: 0;
  width: 100%;
  border: 1px solid rgba(79, 119, 122, 0.5);
}

.border-warning-custom {
  border-color: rgba(255, 102, 14, 0.25);
  border-radius: 0.4rem;
  box-shadow: rgba(219, 54, 13, 0.25) 0px 3px 5px;
}

.mt-minus-1 {
  margin-top: -0.75rem;
}

.link-info-custom {
  color: darkblue;
  &:focus {
    color: #072986d7;
  }
  &:hover {
    color: #072986d7;
  }
}

.row-social {
  margin: 0.3rem 0;
  width: -webkit-fill-available;
  justify-content: center;
}

/* ================== */
